<template>
  <div class="big-table-wrap" ref="tableWrap">
    <div class="search-wrap" ref="searchWrap">
      <Row :gutter="16">
        <Col span="6">
          <div  class="input-item">
            <span class="input-label">消费者账号：</span>
            <Input v-model="userAccount" placeholder="用户账号" class="input-width"></Input>
          </div>
        </Col>
        <Col span="6">
          <div  class="input-item">
            <span class="input-label">消费者内部码：</span>
            <Input v-model="userID" placeholder="用户唯一编码" class="input-width"></Input>
          </div>
        </Col>
        <Col span="6">
          <div  class="input-item">
            <span class="input-label">消费者状态：</span>
            <Select v-model="userStatue" class="input-width">
              <Option v-for="item in statueList" :value="item.child_id" :key="item.child_id" class="input-width">{{ item.child_name }}</Option>
            </Select>
          </div>
        </Col>
        <Col span="6" >
          <div class="input-item">
            <span class="input-label"> 邀请用户账号：</span>
            <Input v-model="inviteUserAcount" placeholder="邀请用户账号" class="input-width"></Input>
          </div>
        </Col>
        <Col span="10" >
            <div  class="input-item">
              <daterange :name="'注册日期'" ref="regTime"  @start-time="timeChange('regStart',$event)"  @end-time="timeChange('regEnd',$event)"></daterange>
            </div>
        </Col>
        <Col span="10">
            <div  class="input-item">
              <daterange :name="'实名日期'" ref="confrimTime" @start-time="timeChange('confrimStart',$event)"  @end-time="timeChange('confrimEnd',$event)"></daterange>
            </div>
        </Col>
        <Col span="2">
          <div  class="input-item right" >
            <Button type="primary" @click="reSearchInfo" >查询</Button>
          </div>
        </Col>
        <Col span="2">
          <div  class="input-item right" >
            <Button type="primary" @click="resetSearch" >重置</Button>
          </div>
        </Col>
      </Row>
    </div>
    <!--操作按钮区域-->
    <div class="handle-wrap" ref="handleWrap" >
      <div class="handle-btn-wrap">
        <Button type="primary" @click="exportInfo">导出</Button>
      </div>
    </div>
    <!--table表格区域-->
    <div class="table-wrap-style" ref="tableBox">
      <Table  :loading="loading" :height="tableHeight" :data="tableData" :columns="tableColumns" stripe border></Table>
    </div>
    <div class="page-wrap" ref="pageWrap">
      <div class="right">
        <Page :total="total" :current="page" @on-change="changePage" @on-page-size-change="changeSize" show-total show-sizer :page-size-opts="pageSizeOpts"></Page>
      </div>
    </div>
  </div>
</template>
<script>
  import { tableMethods } from "../../../js/mixintable";
  import daterange from '../../components/daterange'
 /* import { baseUrl } from '../../../js/env'*/
  export default {
    data () {
      return {
        /*搜索区域 条件*/
        userAccount:'',// 账号
        userID:'', //唯一编码
        userPhone:'', //绑定手机号
        userStatue:'',//状态
        inviteCode:'',//邀请码
        inviteUserAcount:'', //邀请用户账号
        papersNum:'', //证件号码
        loading:false,
        /*用户的状态*/
        statueList:[
          {
            child_name: '全部',
            child_id: ''
          }
        ],
        regStartTime:'', //注册开始时间
        regEndTime:'', //注册结束时间
        comfrimStartTime:'', //实名开始时间
        comfrimEndTime:'', //实名结束时间
        /*table表格*/
        tableColumns: [
          {
            title: '序号',
            align: 'center',
            width:60,
            key:'indexs'
          },
          {
            title: '消费者账号',
            key: 'user_no',
            align: 'center',
            width:150
          },
          {
            title: '消费者内部码',
            key: 'user_seq',
            align: 'center',
          },
          {
            title: '消费者状态',
            align: 'center',
            key: 'user_status',
           /* render:function(h,params){
              return h('span',params.row.child_id_user_status=='51'?'已注册':'已实名');
            }*/
          },
          /*{
            title: '绑定手机号',
            align: 'center',
            key: 'user_phone'
          },*/
          {
            title: '邀请码',
            align: 'center',
            key: 'invite_cv'
          },
          {
            title: '邀请者账号',
            align: 'center',
            key: 'invited_user_no'
          },
          {
            title: '注册时间',
            align: 'center',
            key: 'register_time'
          },
          {
            title: '实名时间',
            align: 'center',
            key: 'confirm_time'
          }
        ]
      }
    },
    components: {
      daterange
    },
    mixins: [tableMethods],
    mounted() {
      this.$nextTick(function () {
        //获取用户状态
        this.getUserState()
        //查询数据
       // this.searchInfo();
        })
    },
    methods: {
      //查询数据
      searchInfo(){
        let that=this
        /*user_no：用户账号；user_seq：用户唯一编码；child_id_user_status：用户状态；user_phone：用户绑定手机号；invite_cv：邀请码
          invite_user_no：邀请用户账号；min_register_time：注册开始时间；max_register_time：注册结束时间
          min_confirm_time：实名开始时间；max_confirm_time：实名结束时间
          pPage：页数；pSize：条数*/
        this.loading=true;
        let objData={
          user_no:this.userAccount,
          user_seq:this.userID,
          child_id_user_status:this.userStatue,
          user_phone:this.userPhone,
          invite_user_no:this.inviteUserAcount,
          invite_cv:this.inviteCode,
          min_register_time:this.regStartTime.length>0?this.regStartTime.replace(/-/g,''):'',
          max_register_time:this.regEndTime.length>0?this.regEndTime.replace(/-/g,''):'',
          min_confirm_time:this.comfrimStartTime.length>0?this.comfrimStartTime.replace(/-/g,''):'',
          max_confirm_time:this.comfrimEndTime.length>0?this.comfrimEndTime.replace(/-/g,''):'',
          pPage:this.page,
          pSize:this.size
        }
        let jsonData = protocol.getJsonParams(protocol.getConsumerList, objData);
        http.ajax('', jsonData, true, (data) => {
          this.loading=false;
         /* this.$Loading.finish();*/
          let resultData=[]
          /*添加序号*/
          if(data.returnList&&data.returnList.length>0){
            data.returnList.forEach(function (item,index) {
              item.indexs=that.page*that.size+index-(that.size-1)
              resultData.push(item)
            })
          }
          this.tableData = resultData;
          this.total =data.total-0;
        })
      },
      //获取用户的状态
      getUserState(){
        let jsonData = protocol.getJsonParams(protocol.getUSerState, {});
        http.ajax('', jsonData, true, (data) => {
          this.loading=false;
          console.log(data)
          this.statueList = this.statueList.concat( data.returnList);
          this.total =data.total-0;
        })
      },
      //重置查询条件
      resetSearch(){
        /*搜索区域 条件*/
        this.userAccount=''// 账号
        this.userID='' //唯一编码
        this.userStatue=''//状态
        this.inviteCode=''//邀请码
        this.inviteUserAcount='' //邀请用户账号
        this.$refs.regTime.resetTime()
        this.$refs.confrimTime.resetTime()
        this.regStartTime='' //注册开始时间
        this.regEndTime='' //注册结束时间
        this.comfrimStartTime='' //实名开始时间
        this.comfrimEndTime='' //实名结束时间
      },
      //获取时间变化并赋值
      timeChange(e,time){
        if(e=="regStart"){
          this.regStartTime=time
        }else if(e=="regEnd"){
          this.regEndTime=time
        }else if(e=="confrimStart"){
          this.comfrimStartTime=time
        }else if(e=="confrimEnd"){
          this.comfrimEndTime=time
        }
      },
      //导出数据
      exportOk(){
        let objData={
          user_no:this.userAccount,
          user_seq:this.userID,
          child_id_user_status:this.userStatue,
          user_phone:this.userPhone,
          invite_user_no:this.inviteUserAcount,
          invite_cv:this.inviteCode,
          min_register_time:this.regStartTime.length>0?this.regStartTime.replace(/-/g,''):'',
          max_register_time:this.regEndTime.length>0?this.regEndTime.replace(/-/g,''):'',
          min_confirm_time:this.comfrimStartTime.length>0?this.comfrimStartTime.replace(/-/g,''):'',
          max_confirm_time:this.comfrimEndTime.length>0?this.comfrimEndTime.replace(/-/g,''):'',
        }
        let jsonData = protocol.getJsonParams(protocol.exportConsumerList, objData);
        http.ajax('', jsonData, true, (data) => {
          window.location.href=loadUrl+data.excel_url
        })
      },
    },
  }
</script>
<style lang="less" scoped>
  @import '../../../css/common.css';
</style>



